/* ---Section Title--- */
.section-title {
  position: relative;
  margin-bottom: 30px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -ms-flex-pack: justify;
  justify-content: space-between;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}

.section-title:before {
  background-color: #e5e5e5;
  content: "";
  height: 1px;
  left: 0;
  position: absolute;
  width: 100%;
  top: 50%;
  -webkit-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  z-index: 1;
}

.section-title>h3 {
  background-color: #ffffff;
  display: inline-block;
  padding-right: 25px;
  margin-bottom: 0;
  z-index: 1;
}

@media (max-width: 767px) {
  .section-title>h3 {
    font-size: 20px;
  }
}

/* ---Product Sticker---*/
.sticker,
.sticker-2 {
  color: #ffffff;
  position: absolute;
  top: 0;
  left: 0;
  padding: 0 10px;
  height: 25px;
  line-height: 26px;
  font-size: 11px;
  text-align: center;
  text-transform: uppercase;
  cursor: text;
}

.sticker:before,
.sticker-2:before {
  -webkit-border-image: none;
  -o-border-image: none;
  border-image: none;
  border-style: solid;
  border-width: 6px;
  content: "";
  margin-top: -6px;
  position: absolute;
  right: -11px;
  top: 50%;
}

.sticker.sticker-2,
.sticker-2.sticker-2 {
  background-color: #242424;
  left: auto;
  right: 0;
}

.sticker.sticker-2:before,
.sticker-2.sticker-2:before {
  border-color: transparent #242424 transparent transparent;
  right: auto;
  left: -11px;
}

