/*-- Scroll To Top --*/
.scroll-to-top {
  background-color: #242424;
  color: #ffffff;
  position: fixed;
  right: 50px;
  bottom: 50px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 100%;
  z-index: 999;
  font-size: 20px;
  overflow: hidden;
  display: block;
  text-align: center;
  opacity: 0;
  visibility: hidden;
  -webkit-animation: kenSlideInDown 1s normal;
  animation: kenSlideInDown 1s normal;
  -webkit-transition: all 300ms ease;
  -o-transition: all 300ms ease;
  transition: all 300ms ease;
}

.scroll-to-top.show {
  opacity: 1;
  visibility: visible;
  -webkit-animation: kenSlideInUp 1s normal;
  animation: kenSlideInUp 1s normal;
}

.scroll-to-top i {
  color: #ffffff;
  font-size: 16px;
  display: block;
}

.scroll-to-top:hover>i {
  -webkit-animation: kenSlideInUp 1s infinite;
  animation: kenSlideInUp 1s infinite;
}

