/* ---Image hover effect--- */
.img-hover_effect {
  position: relative;
}

.img-hover_effect:before {
  bottom: 0;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transition: all 0.5s ease 0s;
  -o-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
  z-index: 1;
}

.img-hover_effect:after {
  bottom: 0;
  content: "";
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  -webkit-transition: all 0.5s ease 0s;
  -o-transition: all 0.5s ease 0s;
  transition: all 0.5s ease 0s;
  z-index: 1;
}

.img-hover_effect:hover:before {
  background-color: rgba(255, 255, 255, 0.3);
  left: 50%;
  right: 50%;
}

.img-hover_effect:hover:after {
  background-color: rgba(255, 255, 255, 0.3);
  bottom: 50%;
  top: 50%;
}

/* ---Global Overlay--- */
.global-overlay {
  background-color: rgba(36, 36, 36, 0.9);
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 99;
  visibility: hidden;
}

